.arrow {
  position: relative;
  display: block;
  width: 45px;
  height: 10px;
  margin: 0 auto;
}
.arrow::before, .arrow::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 35px;
  border-radius: 9999px;
  background-color: #156B8B;
  transform-origin: 50% calc(100% - 0.3px);
}
.arrow::before {
  transform: rotate(65deg);
}
.arrow::after {
  transform: rotate(-65deg);
}

.arrow_1 {
  animation: arrow_1 2s linear infinite;
}

.arrow_2 {
  animation: arrow_2 2s linear infinite;
}

@keyframes arrow_1 {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  10% {
    opacity: 1;
    transform: translateY(0);
  }
  70% {
    opacity: 1;
    transform: translateY(0);
  }
  90%, 100% {
    opacity: 0;
    transform: translateY(10px);
  }
}
@keyframes arrow_2 {
  0%, 20% {
    opacity: 0;
    transform: translateY(0);
  }
  35% {
    opacity: 1;
    transform: translateY(0);
  }
  70% {
    opacity: 1;
    transform: translateY(0);
  }
  90%, 100% {
    opacity: 0;
    transform: translateY(10px);
  }
}
